.hero {
  @apply overflow-hidden;

  & .circle {
    @apply relative z-[1];

    &::before {
      @apply absolute right-[-314px] top-[-426px] z-[-1] h-[768px] w-[801px] bg-cover bg-center bg-no-repeat content-[""];
      @apply xl:right-[-472px] xl:top-[-429px] xl:h-[1087px] xl:w-[1134px] smOnly:hidden;
      background-image: image-set(
        '../images/sections/hero/circle@1x.webp' 1x,
        '../images/sections/hero/circle@2x.webp' 2x
      );
    }
  }

  & .laptops {
    @apply relative;

    &::before {
      @apply absolute bottom-0 z-[-1] h-[105px] w-full bg-cover bg-center bg-no-repeat content-[""] xl:h-[175px] smOnly:hidden;
      background-image: image-set(
        '../images/sections/hero/laptops@1x.webp' 1x,
        '../images/sections/hero/laptops@2x.webp' 2x
      );
    }
  }

  & .form-wrapper {
    @apply bg-green;

    & .title {
      @apply text-white;
    }

    & .subtitle {
      @apply bg-green-100;
    }

    & .timer-wrapper {
      @apply bg-white/50;
    }

    & input {
      @apply border-white text-white placeholder:text-green-500;
    }
  }
}
